body {
  font-family: 'Noto Sans', sans-serif;
  padding: 40px 0;
  background-color: #f8f9fa;
} 

.container {
  max-width: 90%;
  padding: 20px;
  border-radius: 10px;
  margin: 10px auto;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
  background-color: #f4f0f5;
}

h2, h3 {
  text-align: center;
  font-weight: bold;
  color: #faf7f7;
}


.profile-img {
  max-width: 150px;
  height: auto;
  border: 4px solid #fff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
}

#update{
  background-color: #f6f5f7;
  color: #2e022e;
  border-radius: 5px;
}

#update:hover{
  background-color: #ca9ec1;
}

button {
  color: white;
  background-color: #6c008a;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.2s;
}

button:hover {
  background-color: #3f0236;
  transform: translateY(-2px);
}

.table {
  margin-top: 20px;
  width: 100%;
  border-collapse: collapse;
  background-color: #fff;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
}

th, td {
  padding: 12px;
  text-align: center;
  border: 1px solid #ddd;
}

th {
  background-color: #6c008a;
  color: white;
}

tr:nth-child(even) {
  background-color: #f4f1f5;
}

.btn-danger {
  background-color: #d9534f;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
}

.btn-danger:hover {
  background-color: #c9302c;
}

.content-section {
  font-family: "Kumbh Sans", sans-serif;
  background-color: #26023b;
  color: #f5f1f1;
  padding: 20px;
  border-radius: 10px;
}


.loader {
  width: 50px;
  aspect-ratio: 1;
  display: grid;
  border: 4px solid #0000;
  border-radius: 50%;
  border-color: #ccc #0000;
  animation: l16 1s infinite linear;
}
.loader::before,
.loader::after {    
  content: "";
  grid-area: 1/1;
  margin: 2px;
  border: inherit;
  border-radius: 50%;
}
.loader::before {
  border-color: #f03355 #0000;
  animation: inherit; 
  animation-duration: .5s;
  animation-direction: reverse;
}
.loader::after {
  margin: 8px;
}
@keyframes l16 { 
  100%{transform: rotate(1turn)}
}

@media (max-width: 768px) {
  .profile-img {
    max-width: 120px;
  }

  button {
    padding: 8px 15px;
    font-size: 14px;
  }

  .table {
    font-size: 14px;
  }
}
